body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fa-spinner {
  font-size: 6em;
  position: absolute;
  left: 45%;
  top: 80%;
}

.search--bar--input--hint {
  font-size: 0.7em;
}

.logo-marvel {
  width: 10%;
  margin-bottom: 10px;
  margin-top: 10px
}

.header--home {
  font-size: 0.5em;
  position: absolute;
  left: 30%;
}

.header--text, .header--text:hover {
  background-color: #ED1B23;
  font-family: 'Comics';
  padding: 10px;
  border-radius: 10%;
  color: white;
  font-size: 2.8em;
}

.banner {
  background-image: url(https://ae01.alicdn.com/kf/HTB1YJonp5FTMKJjSZFAq6AkJpXal.jpg);
  background-size: cover;
  background-position: center;
  padding: 150px 0;
  margin-left: -15%;
  width: 100vw;
}

.banner p {
  font-size: 20px;
  font-weight: bold;
  color: white;
  opacity: .7;
  text-shadow: 1px 1px 3px rgba(0,0,0,0.2);
}

.search--bar--input {
  width: 50%;
  margin-left: 25%;
}

.pagination {
  margin-top: 5%;
}

.container--cards {
  margin-left: 10%;
}

.card {
  width: 112%;
  margin-top: 10%;
  margin-left: 10%;
}

.card-text-not-available {
  padding-bottom: 24px;
}

.card-img-list {
  height: 250px;
}

.card--img--single {
  width: 100%;
}

.link--to--go, .link--to--go:hover {
  text-decoration: none;
  color: white;
}

.about--hero {
  display: flex;
  padding-top: 5%;
}
